
.jyot_first_page{
    position: relative;
    width: 100%;
    height: 80vh;
    background-image: url('../Resoureces//jyoti_gyb..jpg'); /* Replace 'your-image.jpg' with your image file path */
    background-size: contain;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.jyot_first_page::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: inherit;
    filter: blur(3px); /* Adjust the blur value as needed */
    z-index: -1;
  }
  .jyot_first_page {
    position: relative;
    z-index: 1;
    text-align: center;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.7); /* Add a semi-transparent background to make the text more readable */
  }
  .text_gyb_jyoti_sytle{
    font-size: 80px;
    color: aliceblue;
      
  }
  .jyot_first_page button{
    padding:30px;
    font-size: large;
    background-color: beige;
    border-radius: 30px;
    
  }



/* CSS */
.button-63 {
  display: flex;
  align-items: center;
  background-image: conic-gradient(#553c9a, #ee4b2b, #00c2cb, #553c9a);;
  border: 0;
  border-radius: 8px;
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  color: #FFFFFF;
 
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  justify-content: center;
  line-height: 1em;
  max-width: 100%;
  min-width: 140px;
  padding: 19px 24px;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
  margin-left:100px;
}

.button-63:active,
.button-63:hover {
  outline: 0;
}
.header_gyb_jyoti{
  background-color:#7FD1AE
}
@media screen and (max-width:1000px) {
  .text_gyb_jyoti_sytle{
    font-size: 60px;
    
      
  }
}
@media (min-width: 768px) {
  .button-63 {
    font-size: 24px;
    min-width: 196px;
  }
}
  
