
.text_style_gyb{
    color: #fff;
text-shadow: 15px 10px 10px rgba(0, 0, 0, 0.25);
font-family: Poppins;
font-size: 180px;
font-style: normal;
font-weight: 700;
line-height: 180px; /* 100% */
margin: 0;
margin-right: 0px;
}
.text_style_one{
    color: #1E2328;
font-family: Poppins;
font-size: 180px;
font-style: normal;
font-weight: 700;
line-height: 180px;
margin: 0;
}
@media screen and (max-width:1230px){
   
  
    .img img{
        max-width: 500px;
       
    }
    .left_side{
        margin-left: 20px;
    }
    .text_style_gyb{
       
    font-size: 160px;
   
    }
    .text_style_one{
        font-size: 160px;
    }
    .new_middle_home{
        margin-right: 60px;
    }
}
@media screen and (max-width: 1000px){
 .new_middle_home{
    display:flex;
    justify-content: center;
    align-items: center;
    
}
.text_style_gyb{
       
    font-size: 180px;
   
    }
    .text_style_one{
        font-size: 180px;
    }
    .home_page_ {
        flex-direction: column;
    }
    .img img{
        max-width: 700px;

       
    }
    .left_side {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        margin-left: -100px;
    }
   .new_middle_home{
     margin-left: -120px;
   }
   
   
}
@media screen and (max-width:748px){

   .img img{
    max-width: 620px; /* Set the maximum width of the image to 500px */
    width: 100%; /* Ensure the image scales proportionally */
    height: auto;
   }
    .left_side {
  
    margin-left: -70px;
}
}
@media screen and (max-width: 600px){
   
.text_style_gyb{
   

font-size: 130px;


line-height: 160px; /* 100% */

}
.text_style_one{

font-size: 130px;


line-height: 160px;

}
