
    .branch_full_page {
        background-image: url('../Resoureces/pexels-bri-schneiter-346529.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      

  
  .branch_card {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
    padding: 10px;
  }
  
  .branch_block {
    width: 100%;
    min-height: 40vh;
    max-width: 350px; /* Adjust the maximum width as needed */
    padding: 20px;
    text-align: center;
    color: aliceblue;
    border-radius: 1.3125rem;
    background: rgba(217, 217, 217, 0.07);
    box-shadow: -8.64px 8.64px 8.64px 0px #1E1E1E inset, 8.64px -8.64px 8.68px 0px rgba(139, 139, 139, 0.94) inset;
    backdrop-filter: blur(14.5px);
  }
  
  .branch_block h1 {
    text-transform: uppercase;
    font-size: 2rem; /* Adjust font size as needed */
    font-weight: 600;
    color: #FFF;
  }
  
 .brach_select{
    font-size: 100px;
 }
  
 
  .branch_block p{
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #FFF;
    text-align: center;
  } 
  
  .select_text, .your_text {
    font-size: 2rem; /* Adjust font size as needed */
    font-weight: 600;
    color: #FFC101;
  }
  
  .s_y_w {
    margin: 20px 0;
  }
  
  /* Remove underlines from links */
  a {
    text-decoration: none;
    color: inherit;
  }
  
  /* Adjust link styles if needed */
  a:hover {
    text-decoration: none;
    color: #FFC101; /* Change the color on hover if desired */
  }
  
  /* Media Queries for Responsiveness */
  
  @media screen and (max-width: 768px) {
    .branch_block {
      max-width: 100%; /* Full width on smaller screens */
    }
  
    .branch_block h1 {
      font-size: 1.5rem; /* Adjust font size for smaller screens */
    }
  
    .branch_text_given {
      font-size: 1rem; /* Adjust font size for smaller screens */
    }
  
    .select_text, .your_text {
      font-size: 1.5rem; /* Adjust font size for smaller screens */
    }
  }
  
