.resource_flex_inline{
   display: inline-block;
 
  margin: 0px 30px 0 30px;
 cursor: pointer;
   
}
.resource_edu{
    background-color:aliceblue;
    box-sizing: border-box;
    border-radius: 10px;
    opacity: 0.9;
  /* height: 500px; */
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
   max-width: 420px;
   margin-left: 10px;
   text-align: center;
   font-size: large;

}
.right_edu_dept{
  padding: 4px;
}
.right_edu_dept{
  margin: 0;
}
.img_style_edu{
  margin-top: 34px;
    width: 14.98063rem;
height: 14.98063rem;
flex-shrink: 0;

}
@media screen and (max-width: 1440px) {
    .resource_edu{
        max-width: 30vw;
    }
   .resource_flex_inline{
     margin: 0 10px 10px 10px;
   } 
  }
  @media screen and (max-width: 1260px) {
    .resource_edu{
        max-width: 30vw;
    }
   .resource_flex_inline{
     margin: 0 10px 10px 10px;
   } 
   .right_edu_dept h1{
      margin: 0;
   }
   .img_style_edu{
    width: 13.5rem;
    height: 13.5rem;
   }
  }
  @media  screen and (max-width:1060px) {
   

    .resource_edu
    {
       max-width: 42vw;
        margin: 20px;
        font-size: large
    }
    .img_style_edu{
        width: 14.98063rem;
    height: 14.98063rem;
    margin-top: 20px;
   
    }  
  }
  @media screen  and (max-width:824px){
    .resource_edu{
        max-width: 90vw;
        margin: 10px;
        font-size: larger;
    }
    .img_style_edu{
        width: 16.98063rem;
    height: 16.98063rem;
      margin-top: 40px;
    }
    .right_edu_dept h1 p{
        margin: 0;

    }

  }
  @media screen  and (max-width:550px){
    .resource_edu{
        max-width: 90vw;
        margin: 10px;
        font-size: larger;
    }
    .img_style_edu{
        width: 13.98063rem;
    height: 13.98063rem;
      margin-top: 40px;
    }
    .right_edu_dept h1 p{
