.middle_About_part{
    
    display:flex;
    justify-content:center;
    padding:20px
}
.Middle_About_left{
    width:30vw;
    margin-right: 20px;
}
.para_font_style{
    font-size: 20px;
}
.div_display{
    display:flex; 
      flex-wrap: wrap;
      margin: 20px;
      
}
 .response_div{
  height: 400px;
  width: 300px;
  cursor: pointer;
 }
 .response_div_class{
   height: 100%;
   width: 100%;
 }
 .response_div_class img{
  height: 100%;
  width: 100%;
 }



@media screen and (max-width:1420px){
    .Middle_About_left{
        width:40vw;
        margin-right: 30px;
    }
    .response_div{
     
        width: 420px;
        
       }  
  
  
}
@media screen and (max-width:1320px){
    .Middle_About_left{
        width:33vw;
        margin-right: 22px;
    }
    .response_div{
     
        width: 300px;
        
       }  
  
  
}

@media screen and (max-width:1190px){
    .Middle_About_left{
        width:35vw;
        margin-right: 18px;
    }
    .response_div{
     
        width: 360px;
        
       }  
  
  
}
@media screen and (max-width:1073px){
    .Middle_About_left{
        width:33vw;
        margin-right: 16px;
    }
    .response_div{
     
        width: 340px;
        
       }  
  
  
}


@media screen and (max-width:1000px){
    .middle_About_part{
        flex-direction: column;
        
    }
    .Middle_About_left{
        width:100vw
    }
    .para_font_style{
        font-size: 30px;
        margin: 10px;
    }
  
    .response_div{
     
        width: 280px;
        
       } 
}
@media screen and (max-width:920px){
   
    .response_div{
     
        width: 270px;
        margin: 0;
       }  
       .div_display{
       
          margin: 10px;
          
    }
  
}
@media screen and (max-width:870px){
   
    .response_div{
     
        width: 45vw
        
       }  
    
  
}
@media screen and (max-width:620px){
   
    .response_div{
     
        width: 95%
        
       }  
    
  
}

