.page-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0; /* Set your desired background color */
  }
  
  .content {
    text-align: center;
    background-color: #fff; /* Set the background color for the content */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    max-width: 80%;
  }
  
  .message {
    font-size: 1.2rem;
    color: #333;
    padding: 10px;
    margin: 0;
  }
  
  @media screen and (max-width: 768px) {
    /* Add responsive styles for smaller screens */
    .content {
      max-width: 90%;
    }
  }
  