.footer{
    background-image: url(/src/Resoureces/Group\ 70.png);
    
    background-repeat: no-repeat;
     
     background-size: cover;
     display: flex;
     
     justify-content: space-around;
     align-content: center;
     align-items: center;
     flex-wrap: wrap;

}
.footer_span{
    color: #FFC101;
text-shadow: 15px 4px 4px rgba(0, 0, 0, 0.25);
font-family: Poppins;
font-size: 36px;
font-style: normal;
font-weight: 700;
line-height: normal;
}
.footer_para{
    color: #FFF;
font-family: Poppins;
font-size: 36px;
font-style: normal;
font-weight: 700;
line-height: normal;
margin: 0;
}
.left_footer{
    width: 336px;
    display: flex;
    flex-direction: column;
   
}
.middle_footer{
    
    height: 100%;
    width: 338px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.middle_main{
    background-color: #FFC101;
    opacity: 0.7;
    width: 100%;
    height: 58px;
}
.para_touch{
    color: #000;
font-family: Poppins;
font-size: 20px;
font-style: normal;
font-weight: 700;
line-height: normal;

}
.para_stay{
    color: #FFF;
font-family: Poppins;
font-size: 22px;
font-style: normal;
font-weight: 700;
line-height: normal;

}
.para_mar{
    margin-top: 10px;
}
@media screen and (max-width: 630px){
   
    .para_mar{
        font-size: 20px;
        
    }
    .right_footer img{
        width:368px
    }
}
