/* RunningTime.css */

.running-time {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    font-weight: bold;
   
    
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }
  
  .time-segment {
    margin: 0 10px;
  }
  
  .countdown-complete {
    font-size: 40px;
    font-weight: bold;
    color: #ff5733;
  }
  @media screen and (max-width:535px)
  {
    
.running-time {
   
    font-size: 30px;
   
  }
  .countdown-complete {
    font-size: 30px;
    
  }
  }
  @media screen and (max-width:455px)
  {
    
.running-time {
   
    font-size: 20px;
   
  }
  .countdown-complete {
    font-size: 20px;
    
  }
  }
  
  