@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");


nav{
    display:flex;
    width:100vw

}
.gyb_logo{
    width: 188px;
height: 130px;
flex-shrink: 0;
}
.link{
    display: flex;
    flex-direction: column;
    margin: 40px 0;
    background-color: aliceblue;
     width:70vw;
     height: 64px;

}

.nav_img{
    width:100%;
    height: 35px;
}
.Link_prop{
  width:1241px;
  height:29px;
  display: flex;
  gap:15px;
  margin-left:10px;
}
.text_style{
    color: #000;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.pi_found_se{
    display: flex;
width: 220px;
height: 44px;
padding: 8px 69px 9px 29px;
align-items: center;
flex-shrink: 0;
text-decoration: none;
background-color: #fff;

font-weight: 700;
font-size: medium;
}
.pi_found_se:hover{
    background-color: #FFCE33;
}
.border_to_div{
    border-bottom: 4px;
    border-bottom-color:#FFCE33;

}
.toogle_bar_scroller:hover{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border: 4px;
    border-color: blueviolet;
}
.hamburger_responsive{
    display: none;
}
.links_new_response{
    display: flex;
   
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

@media screen and (max-width: 1000px) {
    nav{
        display: block;

    }
    .logo{

        display: flex;
        justify-content: center;
    }
    .link{
        display:none;
    }
    .hamburger_responsive{
        display: block;
        position: absolute;
        right: 30px;
        top:30px;
        cursor:pointer
    }
    .gyb_logo{
        width: 220px;
    height: 150px;
    
    }
    body {
     height: 100vh;
     width: 100vw;
      }
     
    
  }

